import React, {useState, useEffect} from 'react'
import {Reorder, AnimatePresence} from 'framer-motion'
import { useSelector, useDispatch } from 'react-redux'
import Todo from './Todo'
import { transposedTodos } from '../store/TodoSlice'

const List = () => {

    const todos = useSelector(state => state.todos)
    const visibleList = useSelector(state => state.visibleList)
    const [items,setItems] = useState(todos)
    const dispatch = useDispatch()
    useEffect(() => {
        setItems(todos)
    }, [todos])

    useEffect(() => {
        dispatch(transposedTodos(items)) // чтобы при перестановке элементов в нашем корневом массиве объекты меняли свое положение и не прыгали туда сюда
    }, [items])

    return (
        <AnimatePresence initial={false}>

            {visibleList && <Reorder.Group
                as='div' values={items} onReorder={setItems} className='list'
                initial={{ height: 0, opacity: 0}}
                animate={{height: 'auto', opacity: 1}}
                exit={{height: 0, opacity: 0}}
                transition={{duration: .8}}
                style={{overflow: 'hidden'}}
            >
                {items.map( todo => (
                    <Todo key={todo.id} todo = {todo}/>
                ))}
            </Reorder.Group>}
            
        </AnimatePresence>
    )

}

export default List