import React, { useState } from 'react'
import { motion } from 'framer-motion';
import {IoIosArrowUp} from 'react-icons/io'
import { useDispatch, useSelector } from 'react-redux';
import { addTodo, toggleVisibleList } from '../store/TodoSlice';

const Input = () => {

  const dispatch = useDispatch()
  const [value,setValue] = useState('') // для хранения текста в инпуте
  const isVisible = useSelector(state => state.visibleList) // буливое значения для показа или скрытия списка дел

  const handleSubmit = e => {
    e.preventDefault()
    if (value !== '') dispatch(addTodo({value}))
    setValue('')
  }

  return (
    <div>
      <form onSubmit={handleSubmit} action="#" className='form'>
        <motion.div initial={false} animate={isVisible ? {} :{rotateX: 180}} transition={{duration: 1}}>
          <IoIosArrowUp onClick={() => dispatch(toggleVisibleList())} style={{cursor: 'pointer'}}/>
        </motion.div>
        <input className='input' type="text" value={value} onChange={e => setValue(e.target.value)} placeholder='What needs to be done?' />
      </form>
    </div>
  )

}

export default Input