import React from 'react'
import { useDispatch } from 'react-redux'
import {toggleCompleted, removeTodo} from '../store/TodoSlice'
import {FcCheckmark} from 'react-icons/fc'
import {TiTimesOutline} from 'react-icons/ti'
import { Reorder, AnimatePresence } from 'framer-motion'

const Todo = ({todo}) => {

    const dispatch = useDispatch()    
    const {id,completed,text} = todo

    return (
            <Reorder.Item as='div' value={todo} key={todo} className='todo' initial={{y:-10,opacity:0}} layout  animate={{y:0, opacity:1}}>
                <label htmlFor={id} className='label'>
                    {completed && <FcCheckmark/>}
                    <input type="checkbox" id={id} checked={completed} onChange={() => dispatch(toggleCompleted({id}))} style={{cursor: 'pointer', display: 'none'}}/>
                </label>
                <h3 style={{fontWeight: 300,textDecoration: !completed ? 'none' : 'line-through', color: !completed ? '#000' : 'rgb(202, 202, 202)'}}>{text}</h3>
                <TiTimesOutline onClick={() => dispatch(removeTodo(id))}/>
            </Reorder.Item>
    )

}

export default Todo