import { createSlice } from "@reduxjs/toolkit";
const data = [
    {
        id: Math.random(), 
        completed: false,
        text: 'Привет!'
    },
    {
        id: Math.random(), 
        completed: true,
        text: 'Тестовое задание выполнено'
    },
    {
        id: Math.random(), 
        completed: false,
        text: 'Это приложение использует Redux-Toolkit'
    },
    {
        id: Math.random(), 
        completed: false,
        text: 'А так же использует LocalStorage'
    },
    {
        id: Math.random(), 
        completed: false,
        text: 'А еще есть немного анимации от framer-motion'
    },
]

export const TodoSlice = createSlice({
    name: 'todos',
    initialState: {
        todos: data,
        copyTodos: data,
        type: 'all',
        visibleList: true
    },
    reducers: {
        addTodo: (state,action) => { // добовляет дело
            const newTodo = {
                id: Math.random(),
                text: action.payload.value,
                completed: false
            }
            state.todos.push(newTodo)
            state.copyTodos.push(newTodo)
            if (state.type == 'completed') state.todos = state.todos.filter(item => item.completed)
        },
        removeTodo: (state,action) => { // удаляет дело
            console.log(action)
            state.todos = state.todos.filter(item => item.id !== action.payload)
        },
        toggleCompleted: (state, action) => { // переключает статус дела
            state.todos.forEach(item => {
                if (item.id === action.payload.id) {
                    item.completed = !item.completed
                }
            })
            state.copyTodos = state.todos

        },
        clearCompleted: (state) => { // удаляет из массива выполненные дела
            state.todos = state.copyTodos.filter(item => !item.completed)
            state.type = 'all'
        },
        filterTodos: (state,action) => { // фильтрует маасив тудушек в зависимости от нажатой кнопки
            if (action.payload === 'all') {
                state.todos = state.copyTodos
                state.type = 'all'
            }
            if (action.payload === 'active' && state.type !== 'active') {
                if (state.type == 'completed') {state.todos = state.copyTodos} else state.copyTodos = state.todos
                state.todos = state.todos.filter(item => !item.completed)
                state.type = 'active'
            }
            if (action.payload === 'completed' && state.type !== 'completed') {
                if (state.type == 'active') {state.todos = state.copyTodos} else state.copyTodos = state.todos
                state.todos = state.todos.filter(item => item.completed) // показываем только те у которых есть галочка
                state.type = 'completed'
            }
        },
        toggleVisibleList: (state) => { // скрывает/показывает список дел
            state.visibleList = !state.visibleList
        },
        transposedTodos: (state, action) => { // после перестановки меняет положение элементов в массиве
            state.todos = action.payload
        }
    }
})

export const {
    addTodo,
    removeTodo,
    toggleCompleted,
    clearCompleted,
    filterTodos,
    toggleVisibleList,
    transposedTodos,

} = TodoSlice.actions

export default TodoSlice.reducer