import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { clearCompleted,filterTodos } from '../store/TodoSlice'

const Nav = () => {

  const todos = useSelector(state => state.todos)
  const activeBtn = useSelector(state => state.type)
  const itemsLeft = todos.filter(item =>  !item.completed)
  const dispatch = useDispatch()

  const styleActive = { border: '1px solid #ab59598f'} 
  const funcStyle = type => activeBtn == type ? styleActive : {}
  
  return (
    <div className='nav' >
      <p>{itemsLeft.length} items left</p>
      <div style={{display: 'flex', gap: '5px'}}>
        <button style={funcStyle('all')} onClick={() => dispatch(filterTodos('all'))}>All</button>
        <button style={funcStyle('active')} onClick={() => dispatch(filterTodos('active'))}>Active</button>
        <button style={funcStyle('completed')} onClick={() => dispatch(filterTodos('completed'))}>Completed</button>
      </div>
      <div style={{cursor: 'pointer'}} onClick={() => dispatch(clearCompleted())}>Clear Completed</div>
    </div>
  )

}

export default Nav